"use client";

import React, { useEffect, useState } from "react";
import {
  Input,
  Label,
  Button,
  Card,
  CardContent,
  Select,
  Option,
  Textarea,
  Toaster,
  toast,
} from "@/components/ui";
import PhoneInput from "react-phone-input-2";
import "react-phone-input-2/lib/style.css";
import { DataGrid, GridColDef, GridToolbar } from "@mui/x-data-grid";
import { motion, AnimatePresence } from "framer-motion";
import SaveAsIcon from "@mui/icons-material/SaveAs";
import Image from "next/image";
import { Country } from "country-state-city";

export interface DependantType {
  id: number;
  relationship: string;
  title: string;
  firstName: string;
  middleName: string;
  surname: string;
  idtypes: string;
  idnos: string;
  dob: string;
  gendere: string;
  countrye: string;
  cities: string;
}

export interface FormDataType {
  memberidno: string;
  groupname: string;
  groupnumber: string;
  title: string;
  firstname: string;
  lastname: string;
  middlename: string;
  idtype: string;
  idno: string;
  dateofbirth: string;
  gender: string;
  country: string;
  city: string;
  address: string;
  mobileno: string;
  eimail: string;
  dependantsData: DependantType[];
}

export interface ErrorsType {
  [key: string]: string;
}

export const MemberFormStepper: React.FC = () => {
  const [formData, setFormData] = useState<FormDataType>({
    memberidno: "",
    groupname: "North Wales Kenya Community",
    groupnumber: "NWKC",
    title: "",
    firstname: "",
    lastname: "",
    middlename: "",
    idtype: "",
    idno: "",
    dateofbirth: "",
    gender: "",
    country: "",
    city: "",
    address: "",
    mobileno: "",
    eimail: "",
    dependantsData: [],
  });

  const [errors, setErrors] = useState<ErrorsType>({});
  const [loaderIcon, setLoaderIcon] = useState<boolean>(false);
  const [openDialog, setOpenDialog] = useState<boolean>(false);
  const [currentDependant, setCurrentDependant] = useState<DependantType | null>(null);
  const [dependentCount, setDependentCount] = useState<number>(0);
  const [step, setStep] = useState<number>(1);
  const today = new Date().toISOString().split("T")[0];
  const countries = Country.getAllCountries().map((c) => c.name);

  // Generate member ID
  useEffect(() => {
    const now = new Date();
    const uniqueNumber = `${now.getFullYear()}${now.getMonth() + 1}${now.getDate()}${now.getHours()}${now.getMinutes()}${now.getSeconds()}${now.getMilliseconds()}`;
    const lastSixDigits = uniqueNumber.slice(-6);
    setFormData((prev) => ({ ...prev, memberidno: `Birdview-M${lastSixDigits}` }));
  }, []);

  // Update dependants when count changes
  useEffect(() => {
    setFormData((prev) => {
      const existingDependants = prev.dependantsData;
      const newDependants = Array.from({ length: dependentCount }, (_, idx) => {
        return existingDependants[idx] || {
          id: idx + 1,
          relationship: "",
          title: "",
          firstName: "",
          middleName: "",
          surname: "",
          idtypes: "",
          idnos: "",
          dob: "",
          gendere: "",
          countrye: "",
          cities: "",
        };
      });
      return { ...prev, dependantsData: newDependants };
    });
  }, [dependentCount]);

  const handlePhoneChange = (value: string) => setFormData((prev) => ({ ...prev, mobileno: value }));

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleAddDependant = () => setDependentCount((prev) => prev + 1);

  const handleOpenDialog = (dep: DependantType) => {
    setCurrentDependant(dep);
    setOpenDialog(true);
  };

  const handleCloseDialog = () => {
    setOpenDialog(false);
    setCurrentDependant(null);
    setErrors({});
  };

  const handleDependantChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setCurrentDependant((prev) => (prev ? { ...prev, [name]: value } : prev));
  };

  const handleSaveDependant = () => {
    if (!currentDependant) return;
    const newErrors: ErrorsType = {};
    ["relationship", "title", "firstName", "middleName", "surname", "idtypes", "idnos", "dob", "gendere", "countrye", "cities"].forEach((field) => {
      if (!currentDependant[field as keyof DependantType]) newErrors[field] = `${field} is required`;
    });

    if (Object.keys(newErrors).length > 0) {
      setErrors(newErrors);
      return;
    }

    setFormData((prev) => {
      const exists = prev.dependantsData.some((d) => d.id === currentDependant.id);
      return {
        ...prev,
        dependantsData: exists
          ? prev.dependantsData.map((d) => (d.id === currentDependant.id ? currentDependant : d))
          : [...prev.dependantsData, currentDependant],
      };
    });

    handleCloseDialog();
  };

  const handleSubmit = async () => {
    setLoaderIcon(true);
    try {
      const res = await fetch("/api/kenyans-in-north-wales-member-form", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(formData),
      });

      const data = await res.json();
      if (res.ok) {
        toast({ title: "Success", description: data.message, type: "success" });
        setFormData({
          memberidno: "",
          groupname: "North Wales Kenya Community",
          groupnumber: "NWKC",
          title: "",
          firstname: "",
          lastname: "",
          middlename: "",
          idtype: "",
          idno: "",
          dateofbirth: "",
          gender: "",
          country: "",
          city: "",
          address: "",
          mobileno: "",
          eimail: "",
          dependantsData: [],
        });
        setDependentCount(0);
        setStep(1);
      } else {
        toast({ title: "Error", description: data.error, type: "error" });
      }
    } catch (err: any) {
      toast({ title: "Error", description: err.message, type: "error" });
    } finally {
      setLoaderIcon(false);
    }
  };

  const columns: GridColDef[] = [
    { field: "id", headerName: "No", width: 70 },
    { field: "relationship", headerName: "Relationship", width: 150 },
    { field: "title", headerName: "Title", width: 100 },
    { field: "firstName", headerName: "First Name", width: 130 },
    { field: "middleName", headerName: "Middle Name", width: 130 },
    { field: "surname", headerName: "Surname", width: 130 },
    { field: "idtypes", headerName: "ID Type", width: 120 },
    { field: "idnos", headerName: "ID Number", width: 130 },
    { field: "dob", headerName: "DOB", width: 120 },
    { field: "gendere", headerName: "Gender", width: 100 },
    { field: "countrye", headerName: "Country", width: 130 },
    { field: "cities", headerName: "City", width: 130 },
    {
      field: "action",
      headerName: "Action",
      width: 100,
      renderCell: (params) => (
        <SaveAsIcon style={{ cursor: "pointer", color: "#157EBC" }} onClick={() => handleOpenDialog(params.row as DependantType)} />
      ),
    },
  ];

  return (
    <div className="min-h-screen flex justify-center items-start p-4 bg-gray-50 relative">
      {loaderIcon && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-white/75">
          <div className="loaderIcon flex space-x-1">
            <div className="w-4 h-8 bg-red-500 animate-pulse" />
            <div className="w-4 h-8 bg-blue-500 animate-pulse" />
          </div>
        </div>
      )}

      <Card className="w-full max-w-4xl shadow-xl rounded-2xl overflow-hidden">
        <CardContent>
          <div className="flex flex-col items-center mb-6">
            <Image src="/logo.png" alt="Logo" width={100} height={100} />
            <h1 className="text-2xl font-bold mt-2">Member Registration</h1>
          </div>

          <AnimatePresence exitBeforeEnter>
            {step === 1 && (
              <motion.div
                key="step1"
                initial={{ x: 300, opacity: 0 }}
                animate={{ x: 0, opacity: 1 }}
                exit={{ x: -300, opacity: 0 }}
                transition={{ duration: 0.5 }}
                className="space-y-4"
              >
                <div className="grid grid-cols-2 gap-4">
                  <Input label="Member ID" value={formData.memberidno} readOnly />
                  <Input label="Group Name" value={formData.groupname} readOnly />
                  <Input label="Group Number" value={formData.groupnumber} readOnly />
                  <Select label="Title" name="title" value={formData.title} onChange={handleChange}>
                    <Option value="">Select</Option>
                    <Option value="Mr">Mr</Option>
                    <Option value="Mrs">Mrs</Option>
                    <Option value="Miss">Miss</Option>
                  </Select>
                  <Input label="First Name" name="firstname" value={formData.firstname} onChange={handleChange} />
                  <Input label="Middle Name" name="middlename" value={formData.middlename} onChange={handleChange} />
                  <Input label="Surname" name="lastname" value={formData.lastname} onChange={handleChange} />
                  <Select label="ID Type" name="idtype" value={formData.idtype} onChange={handleChange}>
                    <Option value="">Select</Option>
                    <Option value="Passport">Passport</Option>
                    <Option value="National ID">National ID</Option>
                  </Select>
                  <Input label="ID Number" name="idno" value={formData.idno} onChange={handleChange} />
                  <Input type="date" label="Date of Birth" name="dateofbirth" max={today} value={formData.dateofbirth} onChange={handleChange} />
                  <Select label="Gender" name="gender" value={formData.gender} onChange={handleChange}>
                    <Option value="">Select</Option>
                    <Option value="Male">Male</Option>
                    <Option value="Female">Female</Option>
                  </Select>
                  <Select label="Country" name="country" value={formData.country} onChange={handleChange}>
                    <Option value="">Select</Option>
                    {countries.map((c) => (
                      <Option key={c} value={c}>
                        {c}
                      </Option>
                    ))}
                  </Select>
                  <Input label="City" name="city" value={formData.city} onChange={handleChange} />
                  <Textarea label="Address" name="address" value={formData.address} onChange={handleChange} />
                  <div>
                    <Label>Mobile Number</Label>
                    <PhoneInput country="gb" value={formData.mobileno} onChange={handlePhoneChange} inputClass="w-full" />
                  </div>
                  <Input label="Email" name="eimail" value={formData.eimail} onChange={handleChange} />
                </div>

                <div className="flex justify-end mt-4">
                  <Button onClick={() => setStep(2)}>Next →</Button>
                </div>
              </motion.div>
            )}

            {step === 2 && (
              <motion.div
                key="step2"
                initial={{ x: 300, opacity: 0 }}
                animate={{ x: 0, opacity: 1 }}
                exit={{ x: -300, opacity: 0 }}
                transition={{ duration: 0.5 }}
              >
                <div className="flex justify-between items-center mb-4">
                  <Button onClick={() => setStep(1)}>← Back</Button>
                  <Button onClick={handleAddDependant}>+ Add Dependant</Button>
                </div>

                {dependentCount > 0 && (
                  <div className="h-80">
                    <DataGrid
                      rows={formData.dependantsData}
                      columns={columns}
                      pageSize={5}
                      rowsPerPageOptions={[5]}
                      disableSelectionOnClick
                      components={{ Toolbar: GridToolbar }}
                    />
                  </div>
                )}

                <div className="flex justify-end mt-4">
                  <Button onClick={handleSubmit}>Submit Form</Button>
                </div>
              </motion.div>
            )}
          </AnimatePresence>
        </CardContent>
      </Card>

      <Toaster />

      {openDialog && currentDependant && (
        <Card className="fixed inset-0 m-auto w-3/4 max-w-3xl p-4 bg-white shadow-xl z-50 overflow-auto rounded-xl">
          <CardContent className="grid grid-cols-2 gap-4">
            <Select label="Relationship" name="relationship" value={currentDependant.relationship} onChange={handleDependantChange}>
              <Option value="">Select</Option>
              <Option value="Spouse">Spouse</Option>
              <Option value="Child">Child</Option>
              <Option value="Parent">Parent</Option>
            </Select>
            <Select label="Title" name="title" value={currentDependant.title} onChange={handleDependantChange}>
              <Option value="">Select</Option>
              <Option value="Mr">Mr</Option>
              <Option value="Mrs">Mrs</Option>
              <Option value="Miss">Miss</Option>
            </Select>
            <Input label="First Name" name="firstName" value={currentDependant.firstName} onChange={handleDependantChange} />
            <Input label="Middle Name" name="middleName" value={currentDependant.middleName} onChange={handleDependantChange} />
            <Input label="Surname" name="surname" value={currentDependant.surname} onChange={handleDependantChange} />
            <Select label="ID Type" name="idtypes" value={currentDependant.idtypes} onChange={handleDependantChange}>
              <Option value="">Select</Option>
              <Option value="Passport">Passport</Option>
              <Option value="National ID">National ID</Option>
            </Select>
            <Input label="ID Number" name="idnos" value={currentDependant.idnos} onChange={handleDependantChange} />
            <Input label="Date of Birth" name="dob" type="date" max={today} value={currentDependant.dob} onChange={handleDependantChange} />
            <Select label="Gender" name="gendere" value={currentDependant.gendere} onChange={handleDependantChange}>
              <Option value="">Select</Option>
              <Option value="Male">Male</Option>
              <Option value="Female">Female</Option>
            </Select>
            <Select label="Country" name="countrye" value={currentDependant.countrye} onChange={handleDependantChange}>
              <Option value="">Select</Option>
              {countries.map((c) => (
                <Option key={c} value={c}>
                  {c}
                </Option>
              ))}
            </Select>
            <Input label="City" name="cities" value={currentDependant.cities} onChange={handleDependantChange} />
          </CardContent>
          <div className="flex justify-end mt-4">
            <Button onClick={handleSaveDependant}>Save Dependant</Button>
            <Button variant="ghost" onClick={handleCloseDialog} className="ml-2">
              Cancel
            </Button>
          </div>
        </Card>
      )}
    </div>
  );
};
